import Link from 'next/link'
import Gallery from 'react-photo-gallery'
import Layout from '../components/Layout'
import fetch from 'isomorphic-unfetch'

const Index = (props) => (
  <Layout>
    <h1>GLOBE Media Gallery</h1>
    <Gallery photos={props.photos}/>

  </Layout>
)

Index.getInitialProps = async function() {
  const res = await fetch('https://images-api.nasa.gov/search?q=globe')
  const data = await res.json()
  // const photos = [data.collection.items[0].links[0].href]
  const photos = data.collections.items.map(datum => datum.links[0].href)


  console.log(`Media data fetched. Count: ${data.collection.items.length}`)
  console.log(data.collection.items[0].links[0].href);

  return {
    media: data.collection.items,
    photos: photos
  }
}

export default Index



